'use client';

import React, { useState, useEffect, Suspense } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { ProjectGrid } from '../../components/projects/ProjectGrid';
import { ProjectCreator, type ProjectCreationData } from '../../components/projects/ProjectCreator';
import { ProjectEditorModal } from '../../components/projects/ProjectEditorModal';
import { type ProjectData } from '../../components/projects/ProjectCard';
import { Logger } from '../../../shared/logger';

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: ÌîÑÎ¶¨Ïª¥ÌååÏùºÎêú Ïä§ÌÉÄÏùº ÏÉÅÏàò
const PROJECTS_PAGE_STYLES = {
  container: 'container mx-auto px-4 py-6 max-w-7xl',
  loading: 'flex items-center justify-center min-h-96',
  loadingText: 'text-lg text-slate-600 dark:text-slate-400',
  error: 'flex flex-col items-center justify-center min-h-96 text-center',
  errorTitle: 'text-xl font-semibold text-red-600 dark:text-red-400 mb-2',
  errorMessage: 'text-slate-600 dark:text-slate-400 mb-4',
  retryButton: 'mt-4',
} as const;

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: BE Ïó∞ÎèôÏùÑ ÏúÑÌïú Í∏∞Î≥∏Í∞í (Î°úÎî© Ï§ë ÏÇ¨Ïö©)
const DEFAULT_PROJECTS: readonly ProjectData[] = [] as const;

// üî• Suspense ÎûòÌïëÎêú Ïª¥Ìè¨ÎÑåÌä∏
function ProjectsPageContent(): React.ReactElement {
  const router = useRouter(); // üî• Navigation ÌõÖ Ï∂îÍ∞Ä
  const searchParams = useSearchParams(); // üî• URL ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞ Í∞êÏßÄ
  const [projects, setProjects] = useState<readonly ProjectData[]>(DEFAULT_PROJECTS);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [showCreator, setShowCreator] = useState<boolean>(false);
  const [editingProject, setEditingProject] = useState<ProjectData | null>(null);

  // üî• URL ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú create=true Í∞êÏßÄ Ïãú ÏûêÎèôÏúºÎ°ú ÏÉùÏÑ± Îã§Ïù¥ÏñºÎ°úÍ∑∏ Ïó¥Í∏∞
  useEffect(() => {
    const shouldCreate = searchParams.get('create') === 'true';
    if (shouldCreate) {
      Logger.info('PROJECTS_PAGE', 'üöÄ Auto-opening project creator from URL parameter');
      setShowCreator(true);

      // URLÏóêÏÑú ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞ Ï†úÍ±∞ (ÍπîÎÅîÌïú URL Ïú†ÏßÄ)
      const newUrl = new URL(window.location.href);
      newUrl.searchParams.delete('create');
      window.history.replaceState({}, '', newUrl.pathname);
    }
  }, [searchParams]);

  // üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: Ïù¥ÌéôÌä∏Î°ú Îç∞Ïù¥ÌÑ∞ Î°úÎî©
  useEffect(() => {
    loadProjects();
  }, []);

  // üî• Ïï±ÏúºÎ°ú Î≥µÍ∑Ä/ÌÉ≠ Ìè¨Ïª§Ïä§ Ïãú Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ® (ÏÉùÏÑ± ÌõÑ Îí§Î°úÍ∞ÄÍ∏∞ Îì± Î∞òÏòÅ)
  useEffect(() => {
    const onFocus = () => {
      Logger.debug('PROJECTS_PAGE', 'Window focused - refreshing projects');
      loadProjects();
    };
    const onVisibility = () => {
      if (document.visibilityState === 'visible') {
        Logger.debug('PROJECTS_PAGE', 'Document visible - refreshing projects');
        loadProjects();
      }
    };
    window.addEventListener('focus', onFocus);
    document.addEventListener('visibilitychange', onVisibility);
    return () => {
      window.removeEventListener('focus', onFocus);
      document.removeEventListener('visibilitychange', onVisibility);
    };
  }, []);

  /**
   * üî• Ïã§Ï†ú ÌîÑÎ°úÏ†ùÌä∏ Îç∞Ïù¥ÌÑ∞ Î°úÎî© (BE Ïó∞Îèô) - ÎçîÎØ∏ Îç∞Ïù¥ÌÑ∞ Ï†úÍ±∞
   */
  const loadProjects = async (): Promise<void> => {
    try {
      setLoading(true);
      setError(null);

      // üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: ÌÉÄÏûÖ ÏïàÏ†ÑÌïú IPC ÌÜµÏã†
      const result = await window.electronAPI.projects.getAll();

      // üî• ÏóêÎü¨ Ï≤òÎ¶¨ - IPC ÏùëÎãµ Í≤ÄÏ¶ù
      if (!result.success) {
        throw new Error(result.error || 'Projects API failed');
      }

      // üî• BE Îç∞Ïù¥ÌÑ∞Î•º FE ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
      const projectsData = convertToProjectData(result.data || []);
      setProjects(projectsData);

      Logger.info('PROJECTS_PAGE', `‚úÖ Loaded ${projectsData.length} projects successfully`);

    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'ÌîÑÎ°úÏ†ùÌä∏Î•º Î∂àÎü¨Ïò§Îäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.';
      setError(errorMessage);
      Logger.error('PROJECTS_PAGE', '‚ùå Failed to load projects', err);
      // üî• ÏóêÎü¨ ÏãúÏóêÎèÑ Í∏∞Î≥∏Í∞í ÏÇ¨Ïö©
      setProjects(DEFAULT_PROJECTS);
    } finally {
      setLoading(false);
    }
  };

  /**
   * üî• BE Îç∞Ïù¥ÌÑ∞Î•º FE ProjectData ÌÉÄÏûÖÏúºÎ°ú Î≥ÄÌôò
   */
  const convertToProjectData = (backendProjects: Array<Partial<ProjectData & { lastModified?: Date; createdAt?: Date; updatedAt?: Date }>>): readonly ProjectData[] => {
    return backendProjects.map((project) => ({
      id: project.id || '',
      title: project.title || 'Ï†úÎ™© ÏóÜÏùå',
      description: project.description || '',
      status: project.status || 'draft',
      progress: project.progress || 0,
      createdAt: project.createdAt ? new Date(project.createdAt) : new Date(),
      updatedAt: project.updatedAt ? new Date(project.updatedAt) :
        project.lastModified ? new Date(project.lastModified) : new Date(),
      wordCount: project.wordCount || 0,
      author: project.author || 'ÏÇ¨Ïö©Ïûê',
      genre: project.genre || 'Í∏∞ÌÉÄ'
    }));
  };

  /**
   * üî• Ïã§Ï†ú ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ± (ÎçîÎØ∏ Îç∞Ïù¥ÌÑ∞ Ï†úÍ±∞)
   */
  const handleCreateProject = (): void => {
    Logger.info('PROJECTS_PAGE', 'Opening project creator');
    setShowCreator(true);
  };

  /**
   * üî• ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ± ÏôÑÎ£å Ï≤òÎ¶¨ - ÌîåÎû´ÌèºÎ≥Ñ Î°úÏßÅ Î∞è DB Ïó∞Îèô
   */
  const handleProjectCreated = async (projectData: ProjectCreationData): Promise<void> => {
    try {
      Logger.info('PROJECTS_PAGE', 'üöÄ Creating new project', projectData);

      // üî• ÌîåÎû´ÌèºÎ≥Ñ Ï≤òÎ¶¨ Î°úÏßÅ
      if (projectData.platform === 'import') {
        // ÌååÏùº Î∂àÎü¨Ïò§Í∏∞ ÌîåÎû´Ìèº
        Logger.info('PROJECTS_PAGE', 'üìÅ Importing file for project creation');
        const result = await window.electronAPI.projects.importFile();
        if (result.success && result.data) {
          Logger.info('PROJECTS_PAGE', '‚úÖ Project imported successfully', { projectId: result.data.id });
          // ÏÉùÏÑ±Îêú ÌîÑÎ°úÏ†ùÌä∏ ÏóêÎîîÌÑ∞Î°ú Ï¶âÏãú Ïù¥Îèô
          router.push(`/projects/${result.data.id}`);
          return;
        } else {
          throw new Error(result.error || 'Failed to import project');
        }
      } else if (projectData.platform === 'google-docs') {
        // üî• Google Docs ÌîÑÎ°úÏ†ùÌä∏ÎèÑ Loop Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§Ïóê Ï†ÄÏû•ÌïòÏó¨ Í¥ÄÎ¶¨
        Logger.info('PROJECTS_PAGE', 'üìù Creating Google Docs project in Loop database');

        // Google Docs Ï†ïÎ≥¥Î•º descriptionÏóê JSONÏúºÎ°ú Ï†ÄÏû•
        const googleDocsInfo = {
          originalDescription: projectData.description,
          googleDocId: projectData.googleDocId,
          googleDocUrl: projectData.googleDocUrl,
          isGoogleDocsProject: true
        };

        // Google Docs Ï†ÑÏö© ÌîÑÎ°úÏ†ùÌä∏ Îç∞Ïù¥ÌÑ∞ ÏÉùÏÑ±
        const createData = {
          title: projectData.title,
          description: `${projectData.description}\n\n[Google Docs Ïó∞Îèô Ï†ïÎ≥¥: ${JSON.stringify(googleDocsInfo)}]`,
          genre: projectData.genre,
          content: projectData.content || '# Google Docs Ïó∞Îèô ÌîÑÎ°úÏ†ùÌä∏\n\nÏù¥ ÌîÑÎ°úÏ†ùÌä∏Îäî Google DocsÏôÄ Ïó∞ÎèôÎêòÏñ¥ ÏûàÏäµÎãàÎã§.\n\nÏõêÎ≥∏ Î¨∏ÏÑú ÎßÅÌÅ¨: ' + (projectData.googleDocUrl || ''),
          progress: 0,
          wordCount: projectData.content ? projectData.content.length : 0,
          status: 'active' as const,
          author: 'ÏÇ¨Ïö©Ïûê',
          platform: projectData.platform,
          updatedAt: new Date(),
        };

        const result = await window.electronAPI.projects.create(createData);

        if (!result.success) {
          throw new Error(result.error || 'Failed to create Google Docs project');
        }

        Logger.info('PROJECTS_PAGE', '‚úÖ Google Docs project created successfully in DB', {
          id: result.data?.id,
          googleDocId: projectData.googleDocId,
          title: projectData.title
        });

        // üî• ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ®
        await loadProjects();

        // üî• ÏÉùÏÑ±Îêú ÌîÑÎ°úÏ†ùÌä∏ ÏóêÎîîÌÑ∞Î°ú Ï¶âÏãú Ïù¥Îèô
        if (result.data?.id) {
          Logger.info('PROJECTS_PAGE', 'üöÄ Navigating to new Google Docs project editor', { id: result.data.id });
          router.push(`/projects/${result.data.id}`);
          return;
        }
      }

      // Loop Editor - Ïã§Ï†ú Prisma DBÏóê ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ±
      const createData = {
        title: projectData.title,
        description: projectData.description,
        genre: projectData.genre,
        content: projectData.content || 'ÏÉà ÌîÑÎ°úÏ†ùÌä∏Î•º ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî...\n\n',
        progress: 0,
        wordCount: 0,
        status: 'active' as const,
        author: 'ÏÇ¨Ïö©Ïûê', // TODO: Ïã§Ï†ú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ïó∞Îèô
        platform: projectData.platform,
        updatedAt: new Date(),
      };

      const result = await window.electronAPI.projects.create(createData);

      if (!result.success) {
        throw new Error(result.error || 'Failed to create project');
      }

      Logger.info('PROJECTS_PAGE', '‚úÖ Project created successfully in DB', { id: result.data?.id });

      // üî• ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ®
      await loadProjects();

      // üî• ÏÉùÏÑ±Îêú ÌîÑÎ°úÏ†ùÌä∏ ÏóêÎîîÌÑ∞Î°ú Ï¶âÏãú Ïù¥Îèô (Google Docs Ïä§ÌÉÄÏùº)
      if (result.data?.id) {
        Logger.info('PROJECTS_PAGE', 'üöÄ Navigating to new project editor', { id: result.data.id });
        router.push(`/projects/${result.data.id}`);
        return; // ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Ïù¥ÎèôÌñàÏúºÎØÄÎ°ú Ïó¨Í∏∞ÏÑú Ï¢ÖÎ£å
      }

    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.';
      Logger.error('PROJECTS_PAGE', '‚ùå Failed to create project', err);
      alert(errorMessage);
    }
  };

  const handleViewProject = (project: ProjectData): void => {
    Logger.info('PROJECTS_PAGE', `üîç View project: ${project.id}`, { title: project.title });
    // üî• Ï†ïÏ†Å ÌîÑÎ¶¨Î†åÎçî Í≤ΩÎ°ú ÌïúÍ≥Ñ ÎåÄÏùë: ÏøºÎ¶¨Î°ú Ïã§Ï†ú ID Ï†ÑÎã¨
    router.push(`/projects/new?open=${encodeURIComponent(project.id)}`);
  };

  const handleEditProject = (project: ProjectData): void => {
    Logger.info('PROJECTS_PAGE', `‚úèÔ∏è Edit project (modal): ${project.id}`, { title: project.title });
    setEditingProject(project);
  };

  const handleShareProject = (project: ProjectData): void => {
    Logger.info('PROJECTS_PAGE', `Share project: ${project.id}`, { title: project.title });
    alert('Í≥µÏú† Í∏∞Îä•ÏùÄ Ï§ÄÎπÑ Ï§ëÏûÖÎãàÎã§.');
  };

  const handleDeleteProject = async (project: ProjectData): Promise<void> => {
    Logger.info('PROJECTS_PAGE', `Delete project requested: ${project.id}`, { title: project.title });
    const confirmed = confirm(`Ï†ïÎßêÎ°ú "${project.title}" ÌîÑÎ°úÏ†ùÌä∏Î•º ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?`);
    if (!confirmed) return;
    try {
      const result = await window.electronAPI.projects.delete(project.id);
      if (result.success) {
        setProjects(prev => prev.filter(p => p.id !== project.id));
        Logger.info('PROJECTS_PAGE', `Project deleted: ${project.id}`);
      } else {
        throw new Error(result.error || 'Failed to delete project');
      }
    } catch (err) {
      Logger.error('PROJECTS_PAGE', 'Failed to delete project', err);
      alert('ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
    }
  };

  const handleRetry = (): void => {
    Logger.info('PROJECTS_PAGE', 'Retry loading projects');
    setError(null);
    // Ïª¥Ìè¨ÎÑåÌä∏ Î¶¨ÎßàÏö¥Ìä∏ Ìö®Í≥ºÎ°ú useEffect Ïû¨Ïã§Ìñâ
    setLoading(true);
  };

  if (loading) {
    return (
      <div className={PROJECTS_PAGE_STYLES.container}>
        <div className={PROJECTS_PAGE_STYLES.loading}>
          <div className={PROJECTS_PAGE_STYLES.loadingText}>
            ÌîÑÎ°úÏ†ùÌä∏Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className={PROJECTS_PAGE_STYLES.container}>
        <div className={PROJECTS_PAGE_STYLES.error}>
          <h2 className={PROJECTS_PAGE_STYLES.errorTitle}>Ïò§Î•ò Î∞úÏÉù</h2>
          <p className={PROJECTS_PAGE_STYLES.errorMessage}>{error}</p>
          <button
            onClick={handleRetry}
            className={PROJECTS_PAGE_STYLES.retryButton}
            type="button"
          >
            Îã§Ïãú ÏãúÎèÑ
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className={PROJECTS_PAGE_STYLES.container}>
      <ProjectGrid
        projects={projects}
        onCreateProject={handleCreateProject}
        onViewProject={handleViewProject}
        onEditProject={handleEditProject}
        onShareProject={handleShareProject}
        onDeleteProject={handleDeleteProject}
      />

      {/* üî• ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ± Î™®Îã¨ */}
      <ProjectCreator
        isOpen={showCreator}
        onClose={() => setShowCreator(false)}
        onCreate={handleProjectCreated}
      />

      <ProjectEditorModal
        isOpen={!!editingProject}
        project={editingProject ? { id: editingProject.id, title: editingProject.title, description: editingProject.description, genre: editingProject.genre || 'novel' } : null}
        onClose={() => setEditingProject(null)}
        onUpdated={(u) => {
          setProjects(prev => prev.map(p => p.id === u.id ? { ...p, title: u.title, description: u.description, genre: u.genre, updatedAt: new Date() } : p));
        }}
      />
    </div>
  );
}

// üî• SuspenseÎ°ú ÎûòÌïëÎêú Î©îÏù∏ Ïª¥Ìè¨ÎÑåÌä∏
export default function ProjectsPage(): React.ReactElement {
  return (
    <Suspense fallback={
      <div className={PROJECTS_PAGE_STYLES.loading}>
        <div className={PROJECTS_PAGE_STYLES.loadingText}>ÌîÑÎ°úÏ†ùÌä∏ Î°úÎî© Ï§ë...</div>
      </div>
    }>
      <ProjectsPageContent />
    </Suspense>
  );
}
